.form {
  width: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 15px;
  align-items: flex-start;
  border: 1px solid black;
  padding: 15px;
}

.label {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.input {
  padding: 5px;
}

.button {
  display: block;
  box-sizing: border-box;
  padding: 10px;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0.02em;
}
